.features {

    padding: 95px 0px 129px;

    &__inner {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        @media (max-width:$md4+px){
            flex-direction: column;
            align-items: center;
            text-align: center;
        }
    }

    &__img-wrap {
        width: 50%;

        @media (max-width:$md4+px){
            width: 100%;
        }
    }

    &__img {
        display: block;
        margin: 0 auto;
        max-width: 100%;
        height: auto;
        object-fit: cover;
    }

    &__desc {
        @include padding-top(55px);
        max-width: 428px;
        width: 50%;

        @media (max-width:$md4+px){
            width: 100%;
        }
    }

    &__desc-title {
        margin: 0px 0px 20px 0px;
        @include font-size(35px);
    }

    &__desc-subtitle {
        margin: 0px 0px 40px 0px;
    }

    &__desc-list {
        margin-left: 35px;
        display: inline-block;

        @media (max-width:$md4+px){
            margin-left: 0;
            text-align: left;
        }
    }

    &__list-item {
        margin: 0px 0px 25px 0px;
        position: relative;
        font-size: 14px;

        &:last-child {
            margin: 0;
        }

        &::before{
            content: "";
            position: absolute;
            top: -5px;
            left: -35px;
            width: 24px;
            height: 24px;
            background-image: url("../images/features/icons/check-icon.svg");
            background-repeat: no-repeat;
            background-position: center;
        }
    }
}