.header {
    z-index: 10;
    position: absolute;
    top: 46px;
    right: 0;
    left: 0;
    width: 100%;
    background-color: #fff;

    &.fixed {
        position: fixed;
        top: 0;
        box-shadow: 0px 6px 10px 0px rgba(34, 60, 80, 0.25);

        .header__inner {
            min-height: 55px;
        }
        .header__menu {
            top: 55px;
        }
    }

    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
        min-height: 45px;
    }

    &__logo {
        display: flex;
        align-items: center;
    }

    &__logo-img {
        margin: 0px 10px 0px 0px;
    }

    &__logo-text {
        font-size: 20px;
        font-weight: 500;
    }

    &__menu {
        display: flex;
        @media (max-width:$md4+px){
            z-index: 5;
            overflow: auto;
            position: fixed;
            top: 91px;
            right: 0;
            flex-direction: column;
            padding: 30px;
            width: 100%;
            height: 100%;
            max-width: 330px;
            background-color: $btn-color;
    
            transform-origin: 0% 0%;
            transform: translate3d(100%,0,0);
        
            transition: transform .45s cubic-bezier(.77,.2,.05,1);
    
            &.active {
                transform: translate3d(0,0,0);
            }
        }
        
    }

    &__menu-btns {
        display: none;

        @media (max-width:$md4+px){
            display: flex;
            justify-content: space-around;
        }
    }

    &__menu-btn {
        font-size: 16px;
        font-weight: 500;
    }

    &__btns {
        @media (max-width:$md4+px){
            display: none;
        }
    }
}
.menu {
    display: flex;

    @media (max-width:$md4+px){
        margin: 0px 0px 40px 0px;
        display: block;
    }

    &__item {
        margin: 0px 40px 0px 0px;
        &:last-child {
            margin: 0;
        }

        @media (max-width:$md4+px){
            margin: 0px 0px 20px 0px;

            &:last-child {
                margin: 0;
            }
        }
    }

    &__link {
        color: $text-color;

        @media (max-width:$md4+px){
            display: inline-block;
            color: #fff;

            &:hover {
                text-decoration: underline;
            }
        }
    }
}
.header-btn {
    margin: 0px 30px 0px 0px;
    font-size: 16px;
    font-weight: 500;

    &:last-child {
        margin: 0;
    }
}
.sign-in-btn {
    background-color: transparent;

    @media (max-width:$md4+px){
        color: #fff;
    }
}
.sign-up-btn {
    background-color: transparent;
    color: $btn-color;
    border: 1px solid $btn-color;
    border-radius: 50px;

    padding: 13px 45px;

    &:hover {
        background-color: $btn-color;
        color: #fff;

        @media (max-width:$md4+px){
            border: 1px solid #fff;
            background-color: #ce2e46;
        }
    }

    @media (max-width:$md4+px){
        background-color: #fff;
    }
}
.nav-toggle {
    @media (max-width:$md4+px){
        z-index: 10;
        position: relative;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background-color: $btn-color;

        cursor: pointer;

    &__icon {
        display: inline-block;
        position: absolute;
        left: 50%;
        top: 50%;
        bottom: auto;
        right: auto;
        transform: translateX(-50%) translateY(-50%);
        width: 18px;
        height: 2px;
        background-color: #fff;

        border-radius: 1px;

        &::before, &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;

            border-radius: 1px;
            background-color: inherit;
        }

        &::before {
            bottom: 5px;
        }

        &::after {
            top: 5px;
        }
    }

    &.active {
        .nav-toggle__icon {
            background-color: rgba(#fff, 0);

            &:before, &:after {
                background-color: rgba(#fff, 1);
            }
    
            &:before {
                bottom: 0;
                transform: rotate(45deg);
            }
    
            &:after {
                top: 0;
                transform: rotate(-45deg);
            }
        }
    }
    }
}
