.intro {
    padding: 179px 0px 0px 0px;
    min-height: 865px;

    &__inner {
        position: relative;
        margin: 0px 0px 103px 0px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        @media (max-width:$md4+px){
            margin: 0px 0px 30px 0px;
            text-align: center;
            align-items: center;
            flex-direction: column;
        }
    }

    &__desc {
        width: 50%;
        max-width: 533px;

        @media (max-width:$md4+px){
            margin: 0px 0px 50px 0px;
            width: 100%;
        }
    }

    &__desc-title {
        margin: 0px 0px 20px 0px;
        @include font-size(50px);
        font-weight: 500;
        line-height: 1.5;

        color: $text-color-bold;

        span {
            font-weight: 700;
        }
    }

    &__desc-text {
        margin: 0px 0px 50px 0px;
        line-height: 2;

        color: $text-color;

        span {
            font-weight: 500;
        }
    }

    &__img-wrap {
        width: 50%;
        position: absolute;
        right: -60px;

        @media (max-width:$md4+px){
            position: relative;
            width: 100%;
            right: 0;
        }
    }

    &__img {
        margin: 0 auto;
        display: block;
        max-width: 100%;
        height: auto;
        object-fit: cover;
    }

    &__info {
        position: relative;
        padding: 38px 0px;
        display: flex;
        justify-content: center;

        border-radius: 10px;
        background-color: #fff;

        &::before {
            margin: 0 auto;
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            right: 0;
            opacity: 0.06;
            border-radius: 10px;
            box-shadow: 0 53px 114px #0D1025;
        }

        @media (max-width:$md4+px){
            flex-wrap: wrap;
        }

        @media (max-width:$md3+px){
            flex-direction: column;
            align-content: space-around;
        }
    }

    &__info-item-box {
        padding: 35px 100px;

        display: flex;
        border-right: 2px solid #EEEFF2;

        &:last-child {
            border: none;
        }

        @media (max-width:$md4+px){
            border: none;
        }
    }

    &__info-item {
        display: flex;
        align-items: center;
    }

    &__info-icon {
        margin: 0px 36px 0px 0px;
    }

    &__info-desc {
        display: flex;
        flex-direction: column;
    }

    &__info-desc-num {
        margin: 0px 0px 15px 0px;
        font-size: 25px;
        font-weight: 700;
        color: $text-color-bold;
    }

    &__info-desc-text {
        font-size: 20px;
    }
}

.intro-btn {
    position: relative;

    padding: 18px 77px;
    color: #fff;
    font-weight: 700;
    background-color: $btn-color;
    border-radius: 10px;

    &::before {
        display: block;
        margin: 0 auto;

        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        width: 85%;
        height: 100%;
        opacity: 0.35;
        border-radius: 10px;
        box-shadow: 0 25px 54px $btn-color;
    }
}
