//================================Primary style=================================
body {
    // min-width: 320px;
    overflow-x: hidden;

    font-family: $fontFamily, Arial, Helvetica, sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
    color: $text-color;

    background-color: #fff;
}

body.lock {
    overflow: hidden;
}

button {
    cursor: pointer;
}

a {
    display: block;
}

footer {
    flex-shrink: 0;
}
//================================Primary style=================================

//================================Modules=======================================
.title {
    font-weight: 500;
    font-size: 35px;
    line-height: 2;
    color: $text-color-bold;
}

.subtitle {
    font-weight: 400;
    font-size: 16px;
}

//================================Modules=======================================

//=================================Wrapper=======================================
.wrapper {
    overflow-x: hidden;
    min-height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
}
.page {
    flex: 1 0 auto;
}
//=================================Wrapper=======================================

// [Grid layout]
.container {
    max-width: 1170px;
    padding: 0 15px;    
    margin: 0 auto;
}
// [Grid layout]